body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

/* No CSS *//*# sourceMappingURL=Home.css.map */
nav {
  background-color: #1e272e;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  height: 15vh;
  width: 100%;
  overflow-x: hidden;
}

nav .navbar-brand {
  text-decoration: none;
  color: #f1f2f6;
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

nav .navbar-brand img {
  height: 5rem;
}

nav .nav-links {
  display: flex;
}

nav .nav-links li {
  list-style: none;
}

nav .nav-links li .links {
  color: #f1f2f6;
  text-decoration: none;
  padding: 10px 20px;
  font-family: "Noto Serif", serif;
}

nav .nav-links li .links:hover {
  color: #c9c9c9;
}

nav .nav-links li .active {
  color: #b33939;
  font-weight: 700;
}

nav .nav-links li .active:hover {
  color: #b33939;
}

nav .navbar-toggler {
  width: 60px;
  height: 42px;
  border-radius: 7px;
  border: #f1f2f6 solid 3px;
  background-color: #1e272e;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  display: none;
}

nav .navbar-toggler div.bars {
  width: 30px;
  height: 2px;
  background-color: #f1f2f6;
}

@media only screen and (max-width: 768px) {
  nav .nav-links {
    position: absolute;
    right: 0;
    width: 100%;
    height: 85vh;
    background-color: #1e272e;
    top: 13.5vh;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    transform: translateX(100%);
    transition: all 0.5s ease-in;
  }
  nav .navbar-toggler {
    display: flex;
  }
  nav .nav-open {
    transform: translateX(0%);
  }
}
